.contactFormHeader {
  font-weight: 600;
  font-size: 20px;
  color: gray;
}

.ContactForm {
  background-color: #fff;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70%;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  /* margin: 0 auto; */
  margin-top: 0;
  margin-right: 0;
  position: absolute;
  right: 20px;
  z-index: 100;
}

.field {
  width: 100%;
}

.field input {
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  border-radius: 6px;
  width: 100%;
  height: 40px;
  margin-top: 20px;
  padding: 5px;
  font-size: 15px;
}

.field input:focus {
  outline: none;
}

.field button {
  width: 100%;
  margin-top: 20px;
  background: #ef7917;
  border-radius: 2px;
  color: #fff;
  font-size: 20px;
  padding: 9px;
  border: none;
  outline: none;
  border-radius: 10px;
  cursor: pointer;
}

.ContactForm .field button:focus {
  outline: none;
}

.profilePicker {
  height: 70px;
  width: 70px;
  margin-top: 20px;
}

.profilePicker img {
  height: 100%;
  width: 100%;
  border-radius: 100%;
}

.profilePicker .selectProfileLabel {
  height: 100%;
  width: 100%;
  display: inline-block;
  color: gray;
  background-color: bisque;
  font-weight: 800;
  border: 0;
  outline: none;
  font-size: 50px;
  border-radius: 100%;
  cursor: pointer;
  text-align: center;
  padding: 0;
}

.profilePicker input {
  display: none;
}

.anotherProfilePicker label {
  height: 15px;
  color: gray;
  border: solid 1px gray;
  display: inline-block;
  cursor: pointer;
  padding: 1px;
  background-color: rgb(247, 240, 231);
  font-weight: 500;
  font-size: 12px;
  border-radius: 7px;
}

.anotherProfilePicker input {
  display: none;
}
