.SearchBar {
  height: 70px;
  width: 100%;
  box-sizing: border-box;
  padding-top: 10px;
}

.search-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(229, 223, 210);
  border-radius: 8px;
}

.search-container input {
  height: 43px;
  width: 80%;
  border: none;
  outline: none;
  padding: 0 10px;
  box-sizing: border-box;
  background-color: transparent;
  font-weight: 700;
  font-size: 16px;
}

.search-container input::placeholder {
  color: rgb(185, 146, 119);
  font-weight: 600;
  font-size: 17px;
}

#search-btn {
  background: #eeaa7c;
  height: 25px;
  color: #fff;
  border: none;
  font-size: 18px;
  border-radius: 8px;
  cursor: pointer;
}
