.ReceivingChat {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: first baseline;
  margin-top: 20px;
}

.ReceivingChat .send-chat-container {
  border: solid 1px gray;
  box-shadow: 0 1px 4px 0 #d4d4d5, 0 0 0 1px #f1f1f1;
  border-radius: 15px;
  overflow: hidden;
  width: auto;
  max-width: 80%;
  border-bottom-right-radius: 0;
}

.senderInfo {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: rgb(244, 236, 222);
  padding: 1px;
}

.senderInfo span {
  color: gray;
  font-weight: 500;
  font-size: 12px;
}

.sender-img-container {
  height: 30px;
  width: 30px;
}

.sender-img-container img {
  height: 100%;
  width: 100%;
  border-radius: 100%;
}

.dateAndTime {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}

.dateAndTime span {
  margin: 0 5px 2px 5px;
  color: gray;
  font-weight: 600;
  font-size: 12px;
}

.chatMessage {
  margin: 0;
  padding: 0 7px 0 7px;
}
/* //chat image */

.chatMessage .chatImg {
  height: 350px;
  width: 300px;
}

.chatMessage .chatImg img {
  height: 100%;
  width: 100%;
  border-radius: 5px;
}

.chatMessage p {
  color: gray;
  font-weight: 600;
  font-size: 15px;
  padding: 0;
  margin: 0;
}
