.groupMember {
  margin-top: 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1px 10px;
  border: solid 1px gray;
  box-shadow: 0 1px 4px 0 #d4d4d5, 0 0 0 1px #f1f1f1;
  border-radius: 15px;
  background-color: aliceblue;
}

.groupMember .memberItems .roundedImageContainer {
  height: 35px;
  width: 35px;
  display: inline-block;
}

.groupMember .roundedImageContainer img {
  height: 100%;
  width: 100%;
  border-radius: 100%;
}

.groupMember .memberItems .contactNames {
  font-size: 15px;
  color: green;
  font-weight: 600;
  text-align: center;
  display: inline-block;
  margin-left: 10px;
}
